.formulario{
  display: flex;
  flex-direction: column;
  max-width: 70rem;
  width: 100%;
  margin: 5rem 0;

  input.boton-verde{
      margin: 1rem 0;
      padding: 1rem;
      max-width: 15rem;
      width: 100%;
  }
    legend{
        font-size: 2rem;
        color: $grisOscuro; 
        font-weight: bold;
    }
    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type ="submit"]), 
    textarea, 
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea{
        height: 20rem;
    }
    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}


