html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
}
p {
    color: $negro;
    font-size: 2rem;
}
.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1, h2, h3, p {
    font-family: $fuente_principal;
    margin: 0;
    font-weight: 900;
    color: $azul;
    text-align: center;
}

a:hover {
    color: $blanco;
}

.boton-rojo:hover{
    background-color: darken($rojo,5);
}
.boton-verde:hover{
    background-color: darken($verde,7);
}

.boton-azul:hover{
    background-color: darken($azul,7);
}

input{
    border: none;
}
td{
    text-align: center;
}