.text-center{
    text-align: center;
    
}

.boton-rojo{
    background-color: $rojo;
    color: $blanco;
    font-weight: $bold;
    padding: 5px;   
}

.boton-verde{
    width: 50%;
    background-color: $verde;
    color: $blanco;
    font-weight: $bold;
    padding: 5px;
}

.boton-azul{
    background-color: $azul;
    color: $blanco;
    font-weight: $bold;
    padding: 5px;
    width: 100%;
}

.barra{
    display: flex;
    width: 100%;
    justify-content: space-between;  
    margin: 2rem 0 .5rem 0;
}

.mostrar-empleados{
    display: flex;
    gap: 2rem;
}

.doble{
    display: flex;
    justify-content: center;
    gap: .5rem;
}

.w-auto{
    width: auto;
}

.alerta{
    background-color: $verde;
    font-size: 2rem;
    padding: .5rem;
    text-transform: uppercase;
    font-weight: bold;
    margin: .5rem;
    &.error{
        background-color: $rojo;
        color: $blanco;
    }
}

.margen-0{
    margin: 0;
}